'use client';
import { useState } from 'react';
import Image from 'next/image';

/* Import styles */
import styles from './profile_button_1-pagePart.module.scss';

/* Import media */

import { ChevronDown } from 'lucide-react';
import AvatarImage from '@/media/images/global/avatar_1.svg';

/* Define data type of props */
interface Props {
  ProfileButton1PagePartRCallBack?: (ProfileButton1PagePartRCallBackInfo?: {
    clickedStatus: boolean;
  }) => void;
}

/**
 * A prototype component
 * @example
 * ```tsx
 * <ProfileButton1PagePartR
 *   ProfileButton1PagePartRCallBack={(clickedStatus) => console.log(clickedStatus)}
 * />
 * ```
 */
const ProfileButton1PagePartR: React.FC<Props> = ({
  ProfileButton1PagePartRCallBack,
}) => {
  const [clickedStatusState, setClickedStatusState] = useState(true);

  return (
    <button
      className={`${styles.profile_button_1_pagePart} ${styles.profile_button_1_pagePart_profile}`}
      onClick={() => {
        if (ProfileButton1PagePartRCallBack) {
          ProfileButton1PagePartRCallBack({
            clickedStatus: clickedStatusState,
          });
          setClickedStatusState(!clickedStatusState);
        }
      }}
    >
      <div
        className={`${styles.profile_button_1_pagePart_profile__image_cont} image_cont_type_1`}
      >
        <Image src={AvatarImage} alt="Profile" width={50} height={50} />
      </div>
      <div className={styles.header_6_pagePart__name}>devtz007</div>
      <div
        className={`${styles.profile_button_1_pagePart__icon_cont} ${styles.profile_button_1_pagePart_profile__icon_cont}`}
        style={{
          transform: `rotate(${clickedStatusState ? '180deg' : '0deg'})`,
        }}
      >
        <ChevronDown color="var(--dusty-white, white)" />
      </div>
    </button>
  );
};

ProfileButton1PagePartR.displayName = 'ProfileButton1PagePartR';

export default ProfileButton1PagePartR;
