import { useState, useEffect, ReactSVGElement } from 'react';
import Link from 'next/link';
import Image from 'next/image';

/* Import medias */
import { ArrowRight } from 'lucide-react';
import DemoImage from '@/media/images/global/image_4.png';

/* Import styles */
import styles from './status_cards_1-pagePart.module.scss';

/* Define data type of props */
interface Props {
  cardsP?: {
    imageSrc: string;
    description: string;
    linkHref: string;
    buttonText: string;
    buttonIcon: ReactSVGElement;
  }[];
  StatusCards1PagePartCallBackInfo?: object;
}

const StatusCards1PagePart: React.FC<Props> = ({ cardsP }) => {
  const [activeIndex, setActiveIndex] = useState(0);

  // Default card data to use if no props are provided
  const defaultCards = [
    {
      imageSrc: DemoImage,
      description: 'Checkout our recent update',
      linkHref: '#',
      buttonText: "Let's go",
      buttonIcon: <ArrowRight />,
    },
    {
      imageSrc: DemoImage,
      description: 'Voluptatem minus fuga beatae',
      linkHref: '#',
      buttonText: 'Show',
      buttonIcon: <ArrowRight />,
    },
    {
      imageSrc: DemoImage,
      description: 'Voluptatem minus fuga beatae',
      linkHref: '#',
      buttonText: 'Visit',
      buttonIcon: <ArrowRight />,
    },
  ];

  // Use provided cards data or fallback to default
  const cards = cardsP || defaultCards;

  // Automatically change the active index every 5 seconds
  useEffect(() => {
    const interval = setInterval(() => {
      setActiveIndex((prevIndex) => (prevIndex + 1) % cards.length);
    }, 5000);
    return () => clearInterval(interval);
  }, [cards.length]);

  return (
    <div className={styles.status_cards_1_pagePart}>
      <div className={styles.card}>
        {/* Card image */}
        <div className={`${styles.image_content} image_content_type_1`}>
          <Image
            src={cards[activeIndex].imageSrc}
            alt="Description of image"
            width={50}
            height={50}
            className={styles.image}
          />
        </div>
        {/* Card description */}
        <div className={styles.description}>
          {cards[activeIndex].description}
        </div>

        {/* Card button with link */}
        <Link href={cards[activeIndex].linkHref} className={styles.button}>
          <div className={styles.text}> {cards[activeIndex].buttonText}</div>
          <div className={`${styles.icon} icon_cont_type_1`}>
            {cards[activeIndex].buttonIcon}
          </div>
        </Link>
      </div>

      {/* Card indicators */}
      <div className={styles.indicators}>
        {cards.map((_, index) => (
          <button
            key={index}
            className={`${styles.indicator} ${index === activeIndex ? styles.active : ''}`}
            onClick={() => setActiveIndex(index)}
            role="button"
            tabIndex={index}
          ></button>
        ))}
      </div>
    </div>
  );
};

// Set component display name for debugging purposes
StatusCards1PagePart.displayName = 'StatusCards1PagePart';

export default StatusCards1PagePart;
