'use client';
import { useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';

/* Import media */
import { UserPlus, Bell } from 'lucide-react';
import Logo from '@/media/images/global/logo.png';

/* Import custom components */
import ProfileButton1PagePart from '@/components/button/profile_button_1-pagePart/profile_button_1-pagePart_R';

/* Import styles */
import styles from './header_6_r-pagePart.module.scss';

/**
 * A header component
 *
 * @example
 * ```tsx
 * <Header6RPagePart />
 * ```
 */

const Header6RPagePart: React.FC = () => {
  const [profileButtonDropDownState, setProfileButtonDropDownState] =
    useState(false);
  const [activeItem, setActiveItem] = useState<number>(0);

  const dropdownItems = [
    { name: 'Subscribe', link: '#' },
    { name: 'Settings', link: '#' },
  ];

  return (
    <header className={`${styles.header_6_pagePart} border_type_1`}>
      {/* Logo Section */}
      <Link
        href="/"
        className={`${styles.header_6_pagePart__logo} icon_cont_type_1`}
      >
        <Image
          src={Logo}
          alt="Company Logo"
          width={100}
          height={50}
          className={styles.header_6_pagePart__logo__img}
          priority
        />
      </Link>

      {/* Actions Section */}
      <div className={styles.header_6_pagePart__actions}>
        {/* Invite Link */}
        <Link
          className={`${styles.header_6_pagePart__button} ${styles.header_6_pagePart__button_invite}`}
          href="#"
        >
          <div
            className={`${styles.header_6_pagePart__button__icon_cont} icon_cont_type_1`}
          >
            <UserPlus />
          </div>
          <div className={styles.header_6_pagePart__text}>Invite member</div>
        </Link>

        {/* Divider */}
        <div className="divider_type_1"></div>

        {/* Notification Button */}
        <button
          className={`${styles.header_6_pagePart__button} ${styles.header_6_pagePart__button_notification}`}
        >
          <div
            className={`${styles.header_6_pagePart__button__icon_cont} ${styles.header_6_pagePart__button_notification__icon_cont} icon_cont_type_1`}
          >
            <Bell color="var(--second-text-color, #ffffff)" />
          </div>
          <div
            className={`${styles.header_6_pagePart__button_notification__count}`}
          >
            1
          </div>
        </button>

        {/* Divider */}
        <div className="divider_type_1"></div>

        {/* Profile Button */}
        <ProfileButton1PagePart
          ProfileButton1PagePartRCallBack={(
            ProfileButton1PagePartRCallBackInfo,
          ) => {
            if (ProfileButton1PagePartRCallBackInfo) {
              console.log('ðŸš€ ~ param:', ProfileButton1PagePartRCallBackInfo);
              setProfileButtonDropDownState(
                ProfileButton1PagePartRCallBackInfo.clickedStatus,
              );
            }
          }}
        />
      </div>
      {/* Dynamically add dropdown items */}
      {profileButtonDropDownState && (
        <div className={`${styles.header_6_pagePart__profile_button_dropDown}`}>
          {dropdownItems.map((item, index) => (
            <Link
              key={index}
              href={item.link}
              className={`${styles.header_6_pagePart__profile_button_dropDown__item} ${activeItem === index ? styles.profile_button_dropDown_active : ''}`}
              onClick={(event) => {
                event.preventDefault();
                setActiveItem(index);
              }}
            >
              {item.name}
            </Link>
          ))}
        </div>
      )}
    </header>
  );
};

Header6RPagePart.displayName = 'Header6RPagePart';

export default Header6RPagePart;
