'use client';
import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

/* Import media */
import { Home, Database, File } from 'lucide-react';

/* Import styles */
import styles from './buttons_2-pagePart.module.scss';

// Type for individual navigation button configuration
interface ButtonType {
  buttonText: string;
  value: string;
  svgIcon: React.ReactElement;
  href: string;
}

// Props interface for the component
interface Props {
  buttonsInfoP?: ButtonType[];
  buttons2PagePartCallBack?: (info: { activeButtonInfo: ButtonType }) => void;
}

/**
 * Navigation component that displays a list of interactive buttons with icons.
 *
 * @param buttonsInfoP - Pass the button's info.
 * @buttons2PagePartCallBack - Call the info back.
 *
 * @example
 * ```tsx
 * // Basic usage with custom navigation
 * <Buttons2PagePart
 *   buttonsInfoP={[
 *     {
 *       buttonText: 'Dashboard',
 *       value: 'dashboard',
 *       svgIcon: <Home />,
 *       href: '/dashboard'
 *     },
 *     {
 *       buttonText: 'Settings',
 *       value: 'settings',
 *       svgIcon: <Settings />,
 *       href: '/settings'
 *     }
 *   ]}
 *   buttons2PagePartCallBack={({ activeButtonInfo }) => {
 *     console.log('Navigation changed:', activeButtonInfo.value);
 *   }}
 * />
 *
 * ```
 */
const Buttons2PagePart: React.FC<Props> = ({
  buttonsInfoP,
  buttons2PagePartCallBack,
}) => {
  const pathname = usePathname();

  // Default navigation options if none provided
  const defaultButtonsInfo: ButtonType[] = [
    {
      buttonText: 'Home',
      value: 'home',
      svgIcon: <Home size={24} />,
      href: '/home',
    },
    {
      buttonText: 'Database',
      value: 'database',
      svgIcon: <Database size={24} />,
      href: '/database',
    },
    {
      buttonText: 'Files',
      value: 'files',
      svgIcon: <File size={24} />,
      href: '/files',
    },
  ];

  // Use provided buttons or fall back to defaults
  const buttonsInfo = buttonsInfoP || defaultButtonsInfo;

  return (
    <div className={styles.buttons_2_pagePart}>
      {buttonsInfo.map((item, index) => {
        const isActive =
          pathname === item.href || pathname.startsWith(`${item.href}/`);

        return (
          <Link
            key={index}
            href={item.href}
            className={`${styles.buttons_2_pagePart__button} ${
              isActive ? styles.active : ''
            }`}
            onClick={() => {
              buttons2PagePartCallBack?.({ activeButtonInfo: item });
            }}
          >
            <div
              className={`${styles.buttons_2_pagePart__button__icon} icon_cont_type_1`}
            >
              {item.svgIcon}
            </div>
            {item.buttonText}
          </Link>
        );
      })}
    </div>
  );
};

export default Buttons2PagePart;
